.form {
	justify-content: center;
	position: relative;
	display: inherit;
	white-space: nowrap;
	left: 540px;
	height: 40px;
	width: 40px;
	top: 440px;
}

.output {
	background-color: white;
	position: relative;
	top: 40px;
	left: 20px;
	font-weight: 500;
	font-size: 20px;
	font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana,
		sans-serif;
}
.input {
	width: 200px;
	height: 30px;
	border: 2px solid;
	border-radius: 5px;
	display: inline-block;
}
.select {
	width: 200px;
	height: 35px;
	margin-right: 10px;
	border-radius: 5px;
	border: 2px solid;
	display: inline-block;
}

.contratax {
	display: flex;
	bottom: 120px;
	left: 400px;
	position: fixed;
	z-index: -1;
	width: 700px;
	height: 600px;
}
